@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;700&display=swap);
* {
  font-family: Roboto;
  font-family: "Source Sans Pro", sans-serif;
  margin: 0;
  box-sizing: border-box;
}

.App {
  background-color: chartreuse;
  background-image: url(/memory_game/static/media/wallpaper.3649d562.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  width: 100vw;
}

.header {
  width: 100vw;
  background-color: rgba(0, 0, 0, 0);
  background: linear-gradient(
    rgb(0, 0, 0),
    rgba(0, 0, 0, 0.904) 90%,
    rgba(0, 0, 0, 0)
  );
  padding: 16px;
  color: white;
  text-align: center;
}

.header h1 {
  font-size: 3rem;
}

.header span {
  font-size: 2rem;
  padding: 10px 0 0 0;
  display: block;
}

.winner {
  position: absolute;
  color: red;
  white-space: nowrap;
  font-size: 15vw;
  text-shadow: 0 1px 0 #cccccc, 0 2px 0 #c9c9c9, 0 3px 0 #bbb, 0 4px 0 #b9b9b9,
    0 5px 0 #aaa, 0 6px 1px rgba(0, 0, 0, 0.1), 0 0 5px rgba(0, 0, 0, 0.1),
    0 1px 3px rgba(0, 0, 0, 0.3), 0 3px 5px rgba(0, 0, 0, 0.2),
    0 5px 10px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.2),
    0 20px 20px rgba(0, 0, 0, 0.15);
  top: 50vh;
  left: 50vw;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  animation: 2s grow ease-out;
  -webkit-animation: 2s grow ease-out;
  pointer-events: none;
}

@keyframes grow {
  from {
    font-size: 0;
  }
  to {
    font-size: 15vw;
  }
}

.card {
  width: 20%;
  background-color: rgb(75, 74, 74);
  margin: 20px;
  max-width: 18vw;
  background-color: rgb(17, 51, 0);
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
  overflow: hidden;
  box-shadow: 1px 1px 2px rgba(255, 255, 255, 0.5), -1px 1px 2px rgba(255, 255, 255, 0.5);
}

.card div {
  width: 100%;
}

.card img {
  width: 100%;
  height: 100%;
  max-height: 100%;
}

.card p {
  color: white;
  display: block;
  height: 40px;
  font-size: 1.7rem;
  text-align: center;
  line-height: 35px;
}

@media (max-width: 800px) {
  .card p {
    font-size: 4vw;
  }
}

.container {
  width: 100vw;
  /* background-color: rgb(223, 250, 215); */
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: center;
  max-height: calc(100vh - 142px);
  max-width: calc(360px * 4);
  margin: 0 auto;
}

